import { Dropdown } from './dropdown';

export function SelectPeriod(props) {
  const {
    getSalesInPeriodResponse,
    setDate1,
    setDate2,
    filterByClient,
    setDataClient,
    clientSelected,
    clearFields,
  } = props;

  return (
    <div className="card mb-4 p-3 shadow bg-white rounded">
      <h4 className="mb-3">Busca</h4>
      <form onSubmit={(e) => getSalesInPeriodResponse(e)}>
        <div className="form-row d-flex align-items-center">
          <div className="col mb-3">
            <label>Primeira data</label>
            <input
              type="date"
              className="form-control"
              id="date1"
              onChange={(e) => setDate1(e.target.value)}
              required={false}
            />
          </div>
          <div className="col mb-3">
            <label>Segunda data</label>
            <input
              type="date"
              className="form-control"
              id="date2"
              onChange={(e) => setDate2(e.target.value)}
              required={false}
            />
          </div>
        </div>

        {filterByClient !== null && (
          <div className="mb-4">
            <Dropdown
              title="Selecionar Cliente"
              options={filterByClient}
              selectValue={(e, item) => setDataClient(e, item)}
            />

            {clientSelected && (
              <div className="text-primary font-weight-bold ml-1 mt-3 mb-2">
                Selecionado:{' '}
                <small className="text-muted h6 mb-3">
                  {clientSelected || 'Nenhum'}
                </small>
              </div>
            )}
          </div>
        )}
        <div className="form-row">
          <div className="col">
            <button
              type="button"
              className="btn btn-outline-secondary mb-1 col"
              onClick={clearFields}
            >
              Limpar filtros
            </button>
          </div>
          <div className="col">
            <button type="submit" className="btn btn-primary mb-1 col">
              Buscar
            </button>
          </div>
        </div>
      </form>
    </div>
  );
}
