import '../css/main.css';

import { authService } from '../services/authService';
import { salesService } from '../services/salesService';

import { HTTP_RESPONSE } from '../utils/constants';

import { mask } from '../services/maskMoney';

import React, { useState, useEffect } from 'react';
import { Navigate } from 'react-router-dom';
import { AlertSuccess } from '../components/alerts/alertSuccess';
import { AlertError } from '../components/alerts/alertError';
import { clientService } from '../services/clientService';
import { Dropdown } from '../components/dropdown';
import { Breadcumb } from '../components/breadcumb';

export default function NewSale() {
  const [redirectTo, setRedirectTo] = useState(null);
  const [description, setDescription] = useState('');
  const [price, setPrice] = useState('');
  const [sale, setSale] = useState('');
  const [date, setDate] = useState('');
  const [client, setClient] = useState('');
  const [user_id, setUser_id] = useState('');
  const [serverError, setServerError] = useState(null);

  const [clientsSave, setClientsSave] = useState([]);

  useEffect(() => {
    Promise.all([
      authService.redirectLogin().then((result) => setRedirectTo(result)),
      authService.getUserIdInLocalStorange().then((result) => {
        setUser_id(result);
        clientService
          .findAllClient(result)
          .then((res) => setClientsSave(res.data));
      }),
    ]);
  }, []);

  if (redirectTo) {
    return <Navigate to={redirectTo} />;
  }

  const saveSale = async (event) => {
    event.preventDefault();
    const request = await salesService.newSale(
      user_id,
      description,
      client.trim(),
      price,
      date,
    );

    if (HTTP_RESPONSE.SUCCESS.includes(request.status)) {
      setClient('');
      setDescription('');
      setPrice('');
      setDate('');
      setSale(true);
      return;
    }

    if (request.status === HTTP_RESPONSE.ERROR || !request.status) {
      setServerError(true);
    }
  };

  if (sale) {
    setTimeout(() => setSale(null), 5000);
  }

  if (serverError) {
    setTimeout(() => setServerError(null), 5000);
  }

  return (
    <div className="container-main">
      <Breadcumb
        page={[
          { link: false, name: 'Vendas' },
          { link: false, name: 'Nova Venda' },
        ]}
      />
      <h3 className="title-page">Registrar Nova Venda</h3>
      <form onSubmit={saveSale} className="form-sale">
        <div className="card p-3">
          <div className="form-group">
            <label htmlFor="name">Cliente</label>
            {clientsSave && clientsSave.length > 0 ? (
              <div>
                <Dropdown
                  title="Selecionar Cliente"
                  options={clientsSave}
                  selectValue={(e, item) => setClient(item.name)}
                />
                <div className="text-center pt-2 pb-2">ou</div>
              </div>
            ) : null}
            <input
              type="text"
              className="form-control"
              value={client}
              onChange={(e) => setClient(e.target.value)}
              id="client"
              placeholder="Digite o nome do cliente"
              required={false}
            />
          </div>
          <div className="form-group">
            <label htmlFor="name">Descrio</label>
            <input
              type="text"
              className="form-control"
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              id="name"
              placeholder="Digite a descrio"
              required={true}
            />
          </div>
          <div className="form-group">
            <label htmlFor="price">Preo</label>
            <input
              type="text"
              format="currency"
              className="form-control"
              value={price}
              onChange={(ev) => {
                let val = ev.target.value;
                const { maskedValue } = mask(val, 2, ',', '.', false, 'R$');
                setPrice(maskedValue);
              }}
              id="price"
              placeholder="Digite o preo"
              required={true}
            />
          </div>
          <div className="form-group">
            <label htmlFor="price">Data</label>
            <input
              type="date"
              className="form-control"
              id="date"
              value={date}
              onChange={(e) => setDate(e.target.value)}
              required={true}
            />
            <small className="form-text text-muted">
              Selecione a data da venda.
            </small>
          </div>
          <button type="submit" className="btn btn-primary btn-entrar p-2">
            Salvar
          </button>
          <div className="mt-3">
            {sale === true && (
              <AlertSuccess title="Venda registrada com sucesso." />
            )}
            {sale === false && (
              <AlertError title="Erro ao registrar a venda." />
            )}
            {serverError && (
              <AlertError title="No foi possvel processar a requisio." />
            )}
          </div>
        </div>
      </form>
    </div>
  );
}
